@font-face {
  font-family: "Montserrat-Regular";
  src: url(../fonts/Montserrat-Regular.otf);
}

$grey: #e4e5e7;
$light-grey: #b9bed1;
$dark: #222328;
$dark-text: #1c1e23;
$blue-bg: #2754eb;
$blue-dot: #00deea;
$dark-blue: #343e5c;
$green: #00e2aa;

body, div, img, p, ul, h1, h2, h3, span {
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.container {
  width: 1170px;
  margin: 0 auto;
  &-col {
    display: flex;
    align-items: center;
    &--20 {
      width: 20%;
    }
    &--60 {
      width: 60%;
    }
    &--30 {
      width: 30%;
    }
    &--right {
      justify-content: flex-end;
    }
    &--left {
      justify-content: flex-start;
    }
    &--center {
      justify-content: center;
    }
    &--margin-top {
      margin-top: 30px;
    }
  }
  &--main {
    padding: 50px 0 50px;
  }
  &--row {
    display: flex;
  }
}// .conatainer

.main {
  /* width: 100%; */
  background: #e4e5e7;
}
